<template>
  <div class="page-container" :class="{'centered': !isMobile, 'parallex': isMobile}" :style="{backgroundImage: `url(${$parallexImg})`}">
    <desktop-layout v-if="!isMobile"/>
    <mobile-layout v-else/>
  </div>
</template>

<script>
import pageData from '@/assets/data'
import DesktopLayout from './Desktop'
import MobileLayout from './Mobile'
export default {
  name: 'HomePage',
  components: {
    DesktopLayout,
    MobileLayout
  },
  data () {
    return {
      isMobile: false     
    }
  },
  mounted () {
    const bgType = this.$route.query.bg_type            
    this.isMobile = window.innerWidth < 768
    window.addEventListener("resize", this.screenResizeListener)

  },
  destroy () {
    window.addEventListener("resize", this.screenResizeListener)
  },
  methods: {
    screenResizeListener (e) {
      this.isMobile = window.innerWidth < 768
      console.log(this.isMobile)
    }
  }
}
</script>
<style lang="scss" scoped>
.page-container{  
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &.centered{
    display: flex;
    align-items: center;
    justify-content: center; 
  }   
  &.parallex{
    background-attachment: fixed;
    height: auto;
  }    
}

</style>


