<template>
  <div style="padding: 85px 10px 10px 10px;">
    <div class="top-nav fixed-top bg-white pt-2 pb-2">
      <div class="slide-wrapper">
        <div class="h-slide">
          <div class="row m-0">
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm" v-scroll-to="{el: '#profile', offset: -70}">
                <i class="mdi mdi-account"></i>
                <div>프로필</div>
              </a>          
            </div>
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#education', offset: -70}">
                <i class="mdi mdi-city"></i>
                <div>교육과정</div>
              </a>          
            </div>
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#experience', offset: -70}">
                <i class="mdi mdi-star"></i>
                <div>경력사항</div>
              </a>          
            </div>
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#certificate', offset: -70}">
                <i class="mdi mdi-license"></i>
                <div>자격증</div>
              </a>          
            </div>
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#award', offset: -70}">
                <i class="mdi mdi-trophy"></i>
                <div>수상내역</div>
              </a>          
            </div>
            <div class="col p-0">
              <a href="javascript:;" class="btn btn-block btn-sm vl-line" v-scroll-to="{el: '#etc', offset: -70}">
                <i class="mdi mdi-leaf"></i>
                <div>대외활동</div>
              </a>          
            </div>        
          </div>
        </div>        
      </div>
    </div>
    <div>
      <div class="card mb-5" id="profile">
        <div class="image-wrapper">
          <div class="image" :style="{backgroundImage: `url(${$profileImg})`}"></div>
        </div>
        <div class="card-body">
          <h3 class="text-center mb-3">MinGyeong.Choi</h3>          
          <!-- <div class="social-btns">
            <a href="javascript:;">
              <i class="mdi mdi-instagram"></i>
            </a>
            <a class="ml-3 mr-3" href="javascript:;">
              <i class="mdi mdi-facebook"></i>
            </a>
            <a href="javascript:;">
              <i class="mdi mdi-twitter"></i>
            </a>
          </div> -->
        </div>      
        <div class="card-body pl-5 pr-5">          
          <ul class="personal-info p-0">
            <li>
              <strong class="float-left bg-red text-white">
                Name
              </strong>
              <span>{{ profile.name }}</span>
            </li>
            <li>
              <strong class="float-left bg-red text-white">
                Birth
              </strong>
              <span>{{ profile.birth }}</span>
            </li>
            <li>
              <strong class="float-left bg-red text-white">
                Address
              </strong>
              <span>{{ profile.address }}</span>
            </li>
            <li>
              <strong class="float-left bg-red text-white">
                Email
              </strong>
              <span>{{ profile.email }}</span>
            </li>
          </ul>
          <div class="clearfix"></div>
        </div>

        <div class="card-footer d-block bg-white">
          <a target="_blank" :href="`mailto:${profile.email}`" class="btn btn-block">
            CONTACT ME
            <i class="mdi mdi-send-outline"></i>
          </a>
        </div>
      </div>    

      <div class="card mb-5" id="education">
        <div class="card-header">
          <div><i class="mdi mdi-city"></i> 교육과정</div>
        </div>
        <div class="card-body pl-5 pr-5">
          <vue-aos animation-class="fade-in">  
            <timeline :items="education"/>
          </vue-aos>
        </div>
      </div>

      <div class="card mb-5" id="experience">
        <div class="card-header">
          <div><i class="mdi mdi-star"></i> 경력사항</div>
        </div>
        <div class="card-body pl-5 pr-5">
          <vue-aos animation-class="fade-in">  
            <timeline :items="experience"/>
          </vue-aos>
        </div>
      </div>

      <div class="card mb-5" id="certificate">
        <div class="card-header">
          <div><i class="mdi mdi-license"></i> 자격증</div>
        </div>
        <div class="card-body pl-5 pr-5">
          <vue-aos animation-class="fade-in">  
            <div>
              <div class="mb-2" v-for="(item, index) of certificate" :key="index">
                <div>
                  &middot; {{ item.name }}
                </div>
                <div class="pl-3">
                  <small class="text-secondary"> {{ item.date }} {{ item.company }}</small>
                </div>
              </div>        
            </div>
          </vue-aos>
        </div>
      </div>

      <div class="card mb-5" id="award">
        <div class="card-header">
          <div><i class="mdi mdi-trophy"></i> 수상내역</div>
        </div>
        <div class="card-body pl-5 pr-5">
          <vue-aos animation-class="fade-in">  
            <div>
              <div class="mb-3" v-for="(item, index) of award" :key="index">
                &middot;
                {{ item.name }}
                <small class="text-secondary">
                  <strong>{{ item.company }}</strong>
                  {{ item.date }}
                </small>
              </div>
            </div>
          </vue-aos>          
        </div>
      </div>

      <div class="card mb-5" id="etc">
        <div class="card-header">
          <div><i class="mdi mdi-leaf"></i> 대외활동</div>
        </div>
        <div class="card-body pl-5 pr-5">
          <vue-aos animation-class="fade-in">  
            <div>
              <div class="mb-3" v-for="(item, index) of etc" :key="index">
                <div>
                  <strong>&middot; {{ item.title }}</strong>
                  <small>{{ item.date }}</small>
                </div>                  
                <div v-html="item.desc"></div> 
              </div>
            </div>   
          </vue-aos>             
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import VueAos from 'vue-aos'
import Timeline from './ui/Timeline'
import pageData from '@/assets/data'
export default {
  name: 'MobileLayout',
  components: {
    Timeline,
    VueAos
  },
  data () {
    return {
      profile: pageData.profile,
      education: pageData.education,
      experience: pageData.experience,
      certificate: pageData.certificate,
      award: pageData.award,
      etc: pageData.etc
    }
  }  
}
</script>

<style lang="scss" scoped>
.top-nav{
  box-shadow: 0 0 1px rgba(0, 0, 0, .5);
  .slide-wrapper{
    width: 100%;
    overflow-x: auto;
    .h-slide{
      min-width: 450px;
    }
  }
  .btn{
    font-size: .8rem;
    &:focus{
      box-shadow: none;
      opacity: .8;
    }
  }
}

.card{
  border: 0;
  border-radius: 2px;
  -webkit-box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
}
.vl-line{
  border-left: 1px solid #efefef;
}
.image-wrapper{
  position: relative;
  height: 350px;
  .image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 0;
    overflow: hidden;
    border-radius: 2px;
    &:before,
    &:after{
      content: '';
        position: absolute;
        left: -25%;
        bottom: -63%;
        width: 100%;
        height: 70%;
        background: #fff;
        transform: rotate(12deg);
        -webkit-transform: rotate(12deg);
        -moz-transform: rotate(12deg);
        -o-transform: rotate(12deg);
    }
    &:after{
      left: auto;
      right: -25%;
      transform: rotate(-8deg);
      -webkit-transform: rotate(-8deg);
    }
  }
}

.card-header{
  text-align: center;
  background-color: white;
  padding: 1rem 0;  
  font-size: 1.2rem;  
  font-weight: 500;  
}

.job-desc{
  color: #f54134;
  text-align: center;
}
.social-btns{
  text-align: center;
  a, a:hover{
    color: inherit;        
  }
}

.personal-info{      
  li{
    position: relative;
    padding: 11px 0;        
    display: block;
    vertical-align: top;
    width: auto;    
    word-break: break-word;
    text-align: right;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: -moz-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: -webkit-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: radial-gradient(ellipse at center,#ddd 0%,rgba(255,255,255,0) 70%);       
    }
    strong{
      padding: 1px 8px;
    }
    &:last-child:before{
      height: 0;
    }
  }      
}
</style>


