<template>
  <div class="container">
    <div class="row">
      <div class="col-md-5">
        <div class="left-box">
          <div class="profile-wrapper">
            <div class="card">
              <div class="image-wrapper">
                <div class="image" :style="{backgroundImage: `url(${$profileImg})`}"></div>
              </div>              
              <div class="card-body" style="z-index: 10;">                                
                <h3 class="text-center mb-4">MinGyeong.Choi</h3>               
                <div class="row">
                  <div class="col-10 ml-auto mr-auto">
                    <ul class="personal-info">
                      <li>
                        <strong class="float-left bg-red text-white">
                          Name
                        </strong>
                        <span>{{ profile.name }}</span>
                      </li>
                      <li>
                        <strong class="float-left bg-red text-white">
                          Birth
                        </strong>
                        <span>{{ profile.birth }}</span>
                      </li>
                      <li>
                        <strong class="float-left bg-red text-white">
                          Address
                        </strong>
                        <span>{{ profile.address }}</span>
                      </li>
                      <li>
                        <strong class="float-left bg-red text-white">
                          Email
                        </strong>
                        <span>{{ profile.email }}</span>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="card-footer d-block bg-white" style="z-index: 10;">
                <a target="_blank" :href="`mailto:${profile.email}`" class="btn btn-block">
                  CONTACT ME
                  <i class="mdi mdi-send-outline"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-7">
        <div class="right-box">
          <div class="content-wrapper">
            <!-- <div class="card">
              <div class="card-header">
                <div class="title">
                  <i class="mdi mdi-account"></i>
                  프로필
                </div>
              </div>
              <div class="card-body">
                <div class="text-center">
                  <span v-html="profile.intro"></span>
                </div>
              </div>
            </div> -->

            <div class="card">
              <div class="card-header">
                <div class="title">
                  <i class="mdi mdi-city"></i>
                  교육과정
                </div>
              </div>
              <div class="card-body">
                <div class="pl-3">
                  <timeline :items="education"/>
                </div>
              </div>
            </div>

            <div class="card">
              <div class="card-header">
                <div class="title">
                  <i class="mdi mdi-star"></i>
                  경력사항
                </div>
              </div>
              <div class="card-body">
                <div class="pl-3">
                  <timeline :items="experience"/>
                </div>
              </div>
            </div>

            <div class="card" id="certificate">
              <div class="card-header" style="border:0;">
                <div class="title">
                  <i class="mdi mdi-license"></i>
                  자격증
                </div>
              </div>
              <div class="card-body p-0">
                <div class="pl-4 pr-3">
                  <table class="table">
                    <thead>
                      <tr>
                        <th>취득일</th>
                        <th>자격/어학/수상명</th>
                        <th>발행처/기관</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) of certificate" :key="index">
                        <td>{{ item.date }}</td>
                        <td>{{ item.name }}</td>
                        <td>{{ item.company }}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>

            <div class="card">
              <div class="card-header">
                <div class="title">
                  <i class="mdi mdi-trophy"></i>
                  수상내역
                </div>
              </div>
              <div class="card-body pl-5">
                <div class="mb-3" v-for="(item, index) of award" :key="index">
                  &middot;
                  {{ item.name }}
                  <small class="text-secondary">
                    <strong>{{ item.company }}</strong>
                    {{ item.date }}
                  </small>
                </div>                
              </div>
            </div>
            <div class="card">
              <div class="card-header">
                <div class="title">
                  <i class="mdi mdi-leaf"></i>
                  대외활동
                </div>
              </div>
              <div class="card-body pl-5">
                <div class="mb-3" v-for="(item, index) of etc" :key="index">
                  <div>
                    <strong>&middot; {{ item.title }}</strong>
                    <small>({{ item.date }})</small>
                  </div>                  
                  <div v-html="item.desc"></div>                  
                </div>                
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Timeline from './ui/Timeline'
import pageData from '@/assets/data'
export default {
  name: 'DesktopLayout',
  components: {
    Timeline
  },
  data () {
    return {
      profile: pageData.profile,
      education: pageData.education,
      experience: pageData.experience,
      certificate: pageData.certificate,
      award: pageData.award,
      etc: pageData.etc
    }
  }  
}
</script>

<style lang="scss" scoped>

.left-box,
.right-box{
  height: 670px;
}

.personal-info{      
  margin: 0;
  li{
    position: relative;
    padding: 11px 0;        
    display: block;
    vertical-align: top;
    width: auto;
    font-size: .9rem;
    word-break: break-word;
    text-align: right;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: -moz-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: -webkit-radial-gradient(center,ellipse cover,#ddd 0%,rgba(255,255,255,0) 70%);
      background: radial-gradient(ellipse at center,#ddd 0%,rgba(255,255,255,0) 70%);       
    }
    strong{
      padding: 1px 8px;
    }
    &:last-child:before{
      height: 0;
    }
  }      
}

.image-wrapper{
  position: relative;
  height: 350px;
  .image{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    z-index: 0;
    overflow: hidden;
    &:before,
    &:after{
      content: '';
        position: absolute;
        left: -25%;
        bottom: -63%;
        width: 100%;
        height: 70%;
        background: #fff;
        transform: rotate(12deg);
        -webkit-transform: rotate(12deg);
        -moz-transform: rotate(12deg);
        -o-transform: rotate(12deg);
    }
    &:after{
      left: auto;
      right: -25%;
      transform: rotate(-8deg);
      -webkit-transform: rotate(-8deg);
    }
  }
}

.left-box{
  .profile-wrapper{
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    background-color: white;
    .card{
      position: absolute;
      top: -15px;
      left: 0;
      right: -45px;
      bottom: -15px;
      border: 0;   
      z-index: 3; 
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        box-shadow: 10px 10px 15px rgba(0,0,0,.05);
        border-radius: 0 0 4px;
        -moz-border-radius: 0 0 4px;
        -webkit-border-radius: 0 0 4px;
        -khtml-border-radius: 0 0 4px;
      }              
    }         
    .job-desc{
      color: #f54134;
      text-align: center;
    }
    .social-btns{
      text-align: center;
      a, a:hover{
        color: inherit;        
      }
    }
  }
}

.right-box{  
  background-color: white;
  padding: 1rem 0;
  .content-wrapper{
    border-radius: 4px;
    height: 100%;        
    overflow-y: auto;    
  }
  .card{
    border: 0;
    .card-header{
      background-color: white;
      .title{
        font-size: 1.5rem;
        font-weight: 500;
        padding: 1rem .75rem;
      }
    }
  }
  .lh-line{
    border-left: 1px solid #efefef;
  }
}
#certificate{
  .table{
    font-size: .8rem;
  }
}
</style>


